	module.exports = {"data":{"markdownRemark":{"html":"<h2>Vector Spaces</h2>\n<p>A vector space V is a set of vectors that is closed under vector multiplication and addition.\nThis means that vectors produced from vector addition and multiplication are also within the vector space: </p>\n<ol>\n<li>If a and b are vectors in the space V, then a + b is also in V. </li>\n<li>If c is a scalar and a and b are vectors in V, then ab and ac are also vectors inside V.\nWhen these properties hold true, the vector space is said to be “closed” under vector addition and scalar multiplication. </li>\n</ol>\n<!--This is a stub. <a href='https://github.com/freecodecamp/guides/tree/master/src/pages/mathematics/linear-algebra/vector-spaces/index.md' target='_blank' rel='nofollow'>Help our community expand it</a>.\n\n<!--<a href='https://github.com/freecodecamp/guides/blob/master/README.md' target='_blank' rel='nofollow'>This quick style guide will help ensure your pull request gets accepted</a>.\n\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<ol>\n<li><a href=\"http://mathworld.wolfram.com/VectorSpace.html\">http://mathworld.wolfram.com/VectorSpace.html</a></li>\n<li>\n<p><a href=\"http://www.math.toronto.edu/gscott/WhatVS.pdf\">http://www.math.toronto.edu/gscott/WhatVS.pdf</a></p>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n</li>\n</ol>","fields":{"slug":"/mathematics/linear-algebra/vector-spaces/"},"frontmatter":{"title":"Vector Spaces"}}},"pathContext":{"slug":"/mathematics/linear-algebra/vector-spaces/"}}

